<template>
  <div>
    <a-row :gutter="24" type="flex">
      <a-col :span="24" class="mb-24">
        <x-table
          entityName="indicator-report-card"
          :data="report"
          :columns="columns"
          :actions="['create', 'read', 'edit', 'delete']"
          @delete="deleteItem"
        >
        </x-table>
      </a-col>
    </a-row>
  </div>
</template>

<script>
const columns = [
  {
    title: 'ID',
    dataIndex: 'id',
  },
  {
    title: 'Название',
    dataIndex: 'name',
    filterable: true,
  },
]

export default {
  data() {
    return {
      columns,
    }
  },
  async mounted() {
    await this.getIndicator()
  },
  computed: {
    report() {
      console.log(this.$store.state.app['indicator-report-card'])
      return this.$store.state.app['indicator-report-card']
    },
  },
  methods: {
    async getIndicator() {
      try {
        await this.$store.dispatch('app/getEntities', {
          entityName: 'indicator-report-card',
        })
      } catch (e) {
        console.log(e)
      }
    },
    async deleteItem(id) {
      this.loading = true
      try {
        await this.$store.dispatch('app/deleteItem', {
          entityName: 'indicator-report-card',
          id
        })
        this.$toast.success('Успешно удалено')
      } catch(e) {
        console.log(e)
        this.$errors.showOnce(e.response.data)
      } finally {
        this.loading = false
      }
    }
  },
}
</script>

